import vue from 'vue'
import _ from 'lodash'

const state = {
	orderList:[],
	params: {}
}

const getters = {
	getOrderList: state => state.orderList
}

const actions = {
	fetchOrderList({ commit,state }) {
	  vue.http.post('http://g.cn', state.params)
      .then((res) => {
      	commit('updateOrderList',res.body.getOrderList.list)
      }, (err) => {

      })
	}
}

const mutations = {
	updateOrderList (state,payLoad) {
		state.orderList = payLoad
	},
	updateParams (state,{key,val}) {
		state.params[key]=val
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


